import Vue from 'vue'

const extracts = {
  namespaced: true,
  state: {
    extracts : []
  },
  mutations: {
    FETCH_EXTRACTS (state, extracts) {
      state.extracts = extracts
    }
  },
  actions: {
    getExtracts ({state, commit}) {
        Vue.axios.get(process.env.VUE_APP_API_URL + 'shopify/listextracts')
          .then((response) => {
            commit('FETCH_EXTRACTS', response.data)
          })
          .catch(error => {
            console.log(error.statusText)
          })
    }
  }
}

export default extracts
