// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import 'core-js/es6/promise'
import 'core-js/es6/string'
import 'core-js/es7/array'
// import cssVars from 'css-vars-ponyfill'
import Vue from 'vue'
import axios from 'axios'
import VueAxios from 'vue-axios'
import BootstrapVue from 'bootstrap-vue'
import App from './App'
import router from './router'
import auth from './auth'
import store from './store'

Vue.use(VueAxios, axios)
Vue.use(BootstrapVue)



// Adding token to each axios request
axios.interceptors.request.use(function(config) {
  return auth.getAcessToken().then(token=>{
    config.headers.Authorization = `Bearer ${token}`;
    return Promise.resolve(config);
  })
    .catch(err => {
      return Promise.reject(err)
    });
});

// Checking for user auth before each request. Challenging if not.
router.beforeEach((to, from, next) => {
  auth.getSessionState().then(isSessionActive => {
    if (isSessionActive)
      next();
  });
});


axios.interceptors.response.use(
  (response) => {
    return response
  },
  (error) => {
    const originalRequest = error.config
    // token expired
    if (error.response.status === 401) {
      return auth.manager.signinSilent().then( user => {
        originalRequest.headers['Authorization'] = user.access_token;
        return axios(originalRequest)
      }).catch((error) => {
        auth.signOut();
        return Promise.reject(error);
      });
    }
    return Promise.reject(error)
  });

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  template: '<App/>',
  store: store,
  components: {
    App
  }
})
